.profile {
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    background: #F3F7FB;
    color: var(--secondary);
    margin-bottom: .94em;
}

.profile > * {
    -ms-flex: 1 1;
        flex: 1 1;
}

.profile .profile-info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.profile .profile-info a {
    text-decoration: none;
    color: var(--primary);
}

.profile .profile-info a:hover {
    text-decoration: underline;
}

.profile .profile-info img {
    width: 40px;
    border-radius: 50%;
    margin: 0 1em 0 2.8em;
}

@media (max-width: 710px) {
    .profile .profile-info img {
        margin: 0 1em 0 1em;
    }
}

@media (max-width: 410px) {
    .profile {
        padding: 0 1em;
    }
    .profile .profile-info img {
        margin: 0;
    }
}


